
import { ref, onBeforeMount, reactive } from "vue";
import { ElMessage, ElLoading } from "element-plus";
import {
  Plus,
  Check,
  Printer,
  DocumentCopy,
  Search,
  Setting
} from "@element-plus/icons-vue";
import htmlToPaper from "./plugins/print-html";
import Fuse from "fuse.js";

const BASE_URL = "https://harekrishnasamacar.com/book_hisab";
const fetchOptions = {
  method: "POST",
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json"
  }
};

export default {
  name: "App",
  setup() {
    const tempValue = ref(null);
    const dialogVisible = ref(false);
    const refreshKey = ref(1);
    const column = ref(4);
    const printingData = ref([]);

    const loading = ref(false);
    const searchValue = ref(null);
    const selectedOption = ref({});
    const searchfield = ref();
    const numberField = ref();

    const dialogFormVisible = ref(false);
    const formSubmiting = ref(false);

    const form = reactive({});

    const setFormDefaultValue = () => {
      form.value = {
        name: "",
        en_name: "",
        category_id: "1",
        created_at: ""
      };
    };
    setFormDefaultValue();

    const dialogFormVisible2 = ref(false);
    const savingDate = ref(new Date());

    const reportDialog = ref(false);
    const reportDate = ref(new Date());
    const categoryResult = ref();

    const openReportDialog = async () => {
      await getCategoryResult();
      reportDialog.value = true;
    };

    const toggleDialog = () => {
      dialogVisible.value = !dialogVisible.value;
      printData();
    };

    let tableData = ref([]);
    let totalCount = ref([]);

    const refreshTable = () => {
      // location.reload();
      localStorage.removeItem("booksData");
      refreshKey.value += refreshKey.value;
    };

    let fuse = null;

    onBeforeMount(async () => {
      const loading = ElLoading.service({
        lock: true,
        text: "Loading",
        background: "rgba(250, 250, 250, 0.7)"
      });
      const books = await fetch(`${BASE_URL}/getBook.php`).catch(err => console.log(err));
      const { data, total } = await books.json();
      totalCount.value = total;
      tableData.value = data;
      fuse = new Fuse(tableData.value, {
        // includeScore: true,
        keys: ["name", "en_name"]
      });

      if (localStorage.getItem("booksData")) {
        let previousData = JSON.parse(localStorage.getItem("booksData"));
        tableData.value = tableData.value.map(book => {
          let found = previousData.find(p => p.id === book.id);
          if (found) {
            return {
              ...book,
              amount: found.amount
            };
          }
          return book;
        });
      }
      loading.close();
    });

    const searchBook = (query, cb) => {
      if (query) {
        loading.value = true;
        const result = fuse.search(query);
        loading.value = false;
        cb(result.map(book => book.item));
      }
    };

    const getdata = i => {
      if (tableData.value.length) {
        const count = Math.ceil(tableData.value.length / column.value);
        if (i > 0 && i <= column.value) {
          return tableData.value.slice(count * (i - 1), count * i);
        }
      }
      return [];
    };

    const handleAdd = data => {
      if (tempValue.value) {
        data.amount = Number(data.amount) + tempValue.value;
        tempValue.value = null;
      }
      if (searchValue.value) {
        searchValue.value = "";
        searchfield.value.focus();
      }
    };

    const handleCustomAdd = (num, data) => {
      tempValue.value = num;
      handleAdd(data);
    };

    const addNewBook = () => {
      let book = { ...form };
      // Add Api call to add book to JSON file
      formSubmiting.value = true;
      fetch(`${BASE_URL}/addBook.php`, {
        ...fetchOptions,
        body: JSON.stringify(book)
      })
        .then(res => res.json())
        .then(data => {
          ElMessage({ type: "success", message: data.message });
          formSubmiting.value = false;
          setFormDefaultValue();
          dialogFormVisible.value = false;
          const addedBook = {
            id: data.id,
            name: book.name,
            en_name: book.en_name,
            // sort_key: totalCount,
            amount: 0
          };
          // tableData.value.push(addedBook);
          fuse.add(addedBook);
        })
        .catch(err => {
          ElMessage({ type: "error", message: err.message });
          formSubmiting.value = false;
        });
    };

    const addColumn = () => {
      if (column.value < 8) {
        column.value += 2;
      }
    };

    const removeColumn = () => {
      if (column.value > 2) {
        column.value -= 2;
      }
    };

    const saveMonthlyData = () => {
      formSubmiting.value = true;
      const finalBooksAmount = printingData.value.map(book => ({
        id: book.id,
        date: savingDate.value,
        amount: book.amount
      }));

      fetch(`${BASE_URL}/saveHisab.php`, {
        ...fetchOptions,
        body: JSON.stringify(finalBooksAmount)
      })
        .then(res => res.json())
        .then(data => {
          ElMessage({ type: "success", message: data.message });
          formSubmiting.value = false;
          dialogFormVisible2.value = false;
        })
        .catch(err => {
          formSubmiting.value = false;
          ElMessage({ type: "error", message: err.message });
        });
    };

    const printData = () => {
      printingData.value = tableData.value.filter(r => r.amount);
      saveToLocalStorage(JSON.stringify(printingData.value));
    };

    const printMe = () => htmlToPaper("printable");

    const selectBook = book => {
      selectedOption.value = book;
      console.log(numberField.value);
      if (numberField.value) {
        numberField.value.focus();
      }
    };

    const saveToLocalStorage = data => localStorage.setItem("booksData", data);

    const getCategoryResult = async () => {
      fetch(`${BASE_URL}/getReport.php?date=${reportDate.value.toISOString()}`)
        .then(res => res.json())
        .then(data => (categoryResult.value = data))
        .catch(err => ElMessage({ type: "error", message: err.message }));
    };

    const printPreviousData = async type => {
      try {
        const response = await fetch(
          `${BASE_URL}/getReport.php?type=${type}&date=${reportDate.value.toISOString()}`
        );
        const data = await response.json();
        printingData.value = data;
        dialogVisible.value = true;
      } catch (error) {
        ElMessage({ type: "error", message: err.message });
      }
    };

    return {
      refreshTable,
      totalCount,
      loading,
      selectedOption,
      searchBook,
      searchValue,
      selectBook,
      printMe,
      printData,
      tempValue,
      tableData,
      getdata,

      dialogVisible,

      handleAdd,
      handleCustomAdd,
      toggleDialog,

      printingData,
      refreshKey,
      column,
      addColumn,
      removeColumn,

      dialogFormVisible,
      form,
      addNewBook,
      formSubmiting,
      dialogFormVisible2,
      saveMonthlyData,
      savingDate,
      searchfield,
      numberField,

      // Icons
      Plus,
      Check,
      Printer,
      DocumentCopy,
      Search,
      Setting,

      // Report
      reportDialog,
      reportDate,
      categoryResult,
      getCategoryResult,
      printPreviousData
    };
  }
};
